'use strict';

// MODULES //

import propertyDescriptorIn from '@stdlib/utils-property-descriptor-in' ;


// MAIN //

/**
* Tests if an object's own or inherited property has an accessor descriptor.
*
* @param {*} value - value to test
* @param {*} property - property to test
* @returns {boolean} boolean indicating if an object property has an accessor descriptor
*
* @example
* import defineProperty from '@stdlib/utils-define-property' ;
*
* var obj = {
*     'boop': true
* };
*
* function getter() {
*     return 'beep';
* }
*
* defineProperty( obj, 'beep', {
*     'configurable': false,
*     'enumerable': false,
*     'get': getter
* });
*
* var bool = isAccessorPropertyIn( obj, 'boop' );
* // returns false
*
* bool = isAccessorPropertyIn( obj, 'beep' );
* // returns true
*/
function isAccessorPropertyIn( value, property ) {
	var desc = propertyDescriptorIn( value, property );
	return (
		desc !== null &&
		(
			typeof desc.get === 'function' ||
			typeof desc.set === 'function'
		)
	);
}


// EXPORTS //

export default isAccessorPropertyIn;
